import request from '@/utils/request'

// 登录方法
export function login(user) {
  return request({
    url: '/login',
    headers: {
      isToken: false
    },
    method: 'post',
    data: user
  })
}

// 注册方法
export function register(data) {
  return request({
    url: '/register',
    headers: {
      isToken: false
    },
    method: 'post',
    data: data
  })
}

// 获取用户详细信息
export function getInfo() {
  return request({
    url: '/getInfo',
    headers: {
      isToken: true
    },
    method: 'get'
  })
}
// 获取用户详细路由
export function getRouters() {
  return request({
    url: '/getRouters',
    headers: {
      isToken: true
    },
    method: 'get'
  })
}
// 退出方法
export function logout() {
  return request({
    url: '/logout',
    method: 'post'
  })
}

// 获取验证码
export function getCodeImg() {
  return request({
    url: '/captchaImage',
    headers: {
      isToken: false
    },
    method: 'get',
    timeout: 20000
  })
}